<template>
  <div class="">
    <b-card>
      <b-row>
        <b-col
          lg="12"
          md="12"
          sm="12"
          class="d-flex justify-content-start mb-1"
        >
          <b-form-input
            v-model="formulario[0].nombre"
            size="lg"
            class="font-weight-bold"
            style="background-color:#EFFBF8; color:#0B614B; border-top: 0px; border-bottom: 2px solid #048975; border-left: 0px; border-right: 0px; border-radius:0px "
          />
        </b-col>

        <b-col cols="12">
          <b-form-textarea
            v-model="formulario[0].descripcion"
            placeholder="Sin descripción ..."
            rows="2"
          />
        </b-col>
      </b-row>
    </b-card>

    <br>
    <b-tabs
      content-class="mt-2"
      justified
      align="center"
      class="classTab"
    >
      <b-tab
        title="Preguntas"
        active
        class="pb-2"
      >

        <b-row
          v-for="(item, indexSeccion) in formulario[0].secciones"
          :key="indexSeccion"
        >
          <b-col cols="12">
            <new-seccion
              :index-seccion="indexSeccion"
            />
          </b-col>
        </b-row>
      </b-tab>
      <b-tab
        :title="`Respuestas (${cantRespuestas})`"
      >

        <RespuestasFormularioIndex :id-formulario="idFormularioRespuestas" />
      </b-tab>
      <b-tab title="Configuración">
        <b-card-text>
          Por trabajar
        </b-card-text>

      </b-tab>
      <b-tab title="JSON">
        <b-card-text>
          <pre>{{ formulario }}</pre>
        </b-card-text>

      </b-tab>

    </b-tabs>
  </div>
</template>

<script>
import {
  BTabs, BTab, BCardText, BRow, BCol, BFormInput, BCard, BFormTextarea,
} from 'bootstrap-vue'
import { onUnmounted } from '@vue/composition-api'
import Ripple from 'vue-ripple-directive'
import store from '@/store'
import { mapState } from 'vuex'

import RespuestasFormularioIndex from '@/views/administrador/gestion-formulario/nuevo-formulario/respuestas/RespuestasFormularioIndex.vue'
import NewSeccion from './newSeccion.vue'
import newFormularioStoreModule from './newFormularioStoreModule'

export default {
  components: {
    // BButton,
    BCard,
    BFormTextarea,
    BTabs,
    BTab,
    BRow,
    BCol,
    BFormInput,
    BCardText,

    // extern
    NewSeccion,
    RespuestasFormularioIndex,
  },
  directives: {
    Ripple,
  },

  data() {
    return {
      cantRespuestas: 0,

      idFormularioRespuestas: parseInt(this.$route.params.idFormulario, 10), // id del fromulario para mostrar sus respuestas
    }
  },
  computed: {
    ...mapState('newFormularioModule', ['formulario']),
  },
  created() {
    store.dispatch('newFormularioModule/getFormulario', this.$route.params.idFormulario).then(res => {
      console.log('FORMULARIO INICIO:', res)
      this.formulario.splice(0, 1)

      this.formulario.push(res.data)
    })
  },
  async mounted() {
    await store.dispatch('newFormularioModule/getDataFormulario', this.$route.params.idFormulario)
      .then(res => {
        this.cantRespuestas = res.data.n_respuestas
      })
  },

  methods: {
    actualizarFormulario() {
      store.dispatch('newFormularioModule/updateFormulario', this.formulario).then(res => {
        console.log('¡FORMULARIO ACTUALIZADO!')
        console.log('DATA:', res)
        this.$set(this.formulario, 0, res.data)
      })
    },

    repeateAgain() {
      this.items.push({
        id: this.nextTodoId += this.nextTodoId,
      })
    },

  },

  setup() {
    const NEW_FORMULARIO_STORE_MODULE_NAME = 'newFormularioModule'

    // Register module
    if (!store.hasModule(NEW_FORMULARIO_STORE_MODULE_NAME)) store.registerModule(NEW_FORMULARIO_STORE_MODULE_NAME, newFormularioStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(NEW_FORMULARIO_STORE_MODULE_NAME)) store.unregisterModule(NEW_FORMULARIO_STORE_MODULE_NAME)
    })

    return {
      newFormularioStoreModule,
    }
  },
}
</script>

<style lang="scss" scoped>
.repeater-form {
  overflow: hidden;
  transition: .35s height;
}
</style>
